<template lang="html">
  <div class="orderDetail">
    <el-breadcrumb separator="/" class="app-breadcrumb">
      <el-breadcrumb-item :to="{ name: 'OrderList' }">订单列表</el-breadcrumb-item>
      <el-breadcrumb-item>订单详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form disabled :model="formData" label-width="100px" class="app-form">
      <el-form-item label="产品名称：">
        <el-input v-model="formData.productName"></el-input>
      </el-form-item>
      <el-form-item label="用户名称：">
        <el-input v-model="formData.userName"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<style lang="scss" scoped>
.orderDetail {
  padding: 0 40px;

  .app {
    &-breadcrumb {
      margin-bottom: 40px;
    }

    &-form {
      width: 600px;
    }
  }
}
</style>

<script>
import { mapState, mapActions } from 'vuex'

export default {

  name: 'OrderDetail',
  computed: {
    ...mapState(['orderDetail']),
    formData () {
      return this.orderDetail
    },
  },
  methods: {
    ...mapActions(['getOrderDetail']),
    toOrderList () {
      this.$router.push({
        name: 'OrderList',
      })
    },
  },
  created () {
    const { query } = this.$route
    const { orderId } = query

    if (orderId) {
      this.getOrderDetail({
        orderId,
      })
    }
  },

}
</script>
